@import url(https://fonts.googleapis.com/css?family=Lato&display=swap);
.topnav{position:relative;background-color:black;overflow:hidden}.topnav a{float:left;color:#f2f2f2;text-align:center;padding:14px 16px;text-decoration:none;font-size:17px;-webkit-transition:.5s;transition:.5s}.topnav a:hover{background-color:#ddd;color:black}.topnav a.active{background-color:#4CAF50;color:white;font-size:20px;font-weight:bold}.topnav-centered a{float:none;position:absolute;top:50%;left:50%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%)}.topnav-right{float:right}.hidden{visibility:hidden}@media screen and (max-width: 600px){.topnav a,.topnav-right{float:none;display:block}.topnav-centered a{position:relative;top:0;left:0;-webkit-transform:none;transform:none}}

.boardBox{display:flex;align-items:center;justify-content:center;padding:0;width:calc(250px / 3);height:calc(250px / 3);font-size:32px;color:#111;background-color:#fff;border:1px solid #aaa}

div.popup{position:fixed;top:0;left:0;height:100vh;width:100vw;z-index:10;background:rgba(0,0,0,0.7)}div.popup div.popup-wrapper{position:fixed;top:50%;left:50%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%);background-color:black;border-style:solid;border-width:1px;border-color:#4CAF50;border-radius:20px;min-width:80%;min-height:30%;text-align:center;display:flex;justify-content:center;align-items:center}div.popup div.popup-wrapper h2{padding-bottom:30px}div.popup div.popup-wrapper a#btnPopupNewGame{text-decoration:none;padding:12px 16px;font-size:18px;color:white;background-color:#4CAF50;border:0;border-radius:4px;cursor:pointer;-webkit-transition:.5s;transition:.5s;text-transform:capitalize;font-weight:bold}div.popup div.popup-wrapper a#btnPopupNewGame:hover,div.popup div.popup-wrapper a#btnPopupNewGame:active{background-color:white;color:#4CAF50;border-style:solid;border-width:1px;border-color:#4CAF50}

.board-wrapper{display:flex;flex-wrap:wrap;flex-direction:row;justify-content:center;align-items:start;align-content:center;height:90vh}.board-wrapper .board-item{flex:0 0 auto;margin:80px}.board-wrapper .board-item .board{width:250px}.board-wrapper .board-item .board .board-heading{margin-top:0;margin-bottom:8px;font:700 18px / 1.618 'Lato';list-style-type:none}.board-wrapper .board-item .board .board-row{display:flex;flex-flow:row wrap}.board-wrapper .board-item .board-history{background-color:black;min-width:250px;min-height:300px;border-style:solid;border-width:1px;border-color:#4CAF50;border-radius:20px}.board-wrapper .board-item .board-history .board-heading{margin-top:10px;margin-bottom:8px;font:700 18px / 1.618 'Lato';list-style-type:none;text-align:center;text-transform:capitalize}.board-wrapper .board-item .board-history ul.board-history-list{padding:0;list-style-type:none}.board-wrapper .board-item .board-history ul.board-history-list p{margin-top:20px;text-align:center;width:100%}

div#wrapper-start-game{padding:100px 0px;text-align:center}div#wrapper-start-game a.btn-start-game{text-decoration:none;padding:12px 16px;font-size:18px;color:white;background-color:#4CAF50;border:0;border-radius:4px;cursor:pointer;-webkit-transition:.5s;transition:.5s;text-transform:capitalize;font-weight:bold}div#wrapper-start-game a.btn-start-game:hover,div#wrapper-start-game a.btn-start-game:active{background-color:white;color:#4CAF50;border-style:solid;border-width:1px;border-color:#4CAF50}div#wrapper-recent-games{background-color:black;min-height:400px;max-width:90%;margin-left:auto;margin-right:auto;padding:15px;border-radius:20px;border-style:solid;border-width:1px;border-color:#4CAF50}div#wrapper-recent-games h2{text-align:center;text-transform:uppercase}div#wrapper-recent-games ul{text-align:center;counter-reset:index;max-width:60%;padding:0;margin-left:auto;margin-right:auto}div#wrapper-recent-games ul li{counter-increment:index;display:flex;align-items:center;padding:12px 0;box-sizing:border-box}div#wrapper-recent-games ul li:before{content:counters(index, ".", decimal-leading-zero);font-size:1.5rem;text-align:right;font-weight:bold;min-width:50px;padding-right:12px;font-feature-settings:"tnum";font-variant-numeric:tabular-nums;align-self:flex-start;background-image:-webkit-gradient(linear, left top, left bottom, from(aquamarine), to(orangered));background-image:linear-gradient(to bottom, aquamarine, orangered);background-attachment:fixed;-webkit-background-clip:text;-webkit-text-fill-color:transparent}div#wrapper-recent-games ul li+li{border-top:1px solid rgba(255,255,255,0.2)}

body{margin:0;font-family:'Lato', sans-serif;background-color:#171717;color:white}.btn{padding:12px 16px;margin-top:18px;font-size:14px;color:#fff;background-color:#3498db;border:0;border-radius:4px;cursor:pointer;-webkit-transition:background-color .25s ease-in-out;transition:background-color .25s ease-in-out}.btn:hover{background-color:#2980b9}

